html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  background-color: #dfd7d7;
}

#app {
  width: 299px;
  height: 396px;
  display: grid;
  grid-template-rows: 1.5fr 1fr 1fr 1fr 1fr 1fr;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 1px;
  margin: 120px auto;
  font-family: 'Roboto Mono', monospace;
  font-size: 18px;
  color: white;
  background-color: black;
}

#display {
  display: flex;
  flex-direction: column;
  
  grid-column-start: 1;
  grid-row-start: 1;
  grid-column-end: 5;
  grid-row-end: 2;
  padding: 8px;
  color: #ffc600;
  background-color: #333;
  text-align: right;
  font-size: 20px;
}

.output {
  flex: 1;
}

#clear {
  grid-column-start: 1;
  grid-row-start: 2;
  grid-column-end: 3;
  grid-row-end: 3;
}

#zero {
  grid-column-start: 1;
  grid-row-start: 6;
  grid-column-end: 3;
  grid-row-end: 7;
}

#equals {
  grid-row-start: 5;
  grid-column-start: 4;
  grid-row-end: 7;
  grid-column-end: 5;
}

.btn {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #585858;
}

.ops {
  background-color: #da670a;
}

.result {
  font-size: 36px;
}